.AllListChambre {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-content: flex-start;
	align-items: flex-start;

	div {
		width: 190px;
		margin: 2px;
	}

	.cardChambre {
		background-color: #fefefe;
		width: 100%;
		padding: 5px;
		&:hover{
			box-shadow: 0 3px 5px -1px rgba(0,0,0,0.2), 0 6px 10px 0 rgba(0,0,0,0.14), 0 1px 18px 0 rgba(0,0,0,0.12);
			transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,border 250ms cubic-bezier(0.4, 0, 0.2, 1) 0;
		}
		.MuiFab-extended.MuiFab-sizeSmall {
			border-radius: 3px;
		}

		img {
			width: 100%;
			&:hover{
				animation: scal 1s ease-in forwards;
			}
		}
		@keyframes scal {
			0% {
				transform: scale(1);
			}
			50% {
				transform: scale(1.1);
			}
			100% {
				transform: scale(1.03);
			}
		}
	}
}



.loading {
	animation: slides3 1s ease-in forwards;
	animation-iteration-count: infinite;
}
@keyframes slides3 {
	0% {
		transform: scale(0.5);
		transform: opacity(0.5);
	}
	20% {
		transform: scale(1.9);
		transform: opacity(0.8);
	}
	30% {
		transform: scale(1);
		transform: opacity(0.5);
	}
	70% {
		transform: scale(1.5);
		transform: opacity(0.8);
	}
	100% {
		transform: scale(1.7);
		transform: opacity(0.5);
	}
}
